@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: black;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
  font-size: 80px;
  color:rgba(252, 247, 255, 0.651); 
  text-shadow: 0 0 1px rgba(157,208,154,.3); 
  /* Fallback for nonCSS3? */
  /* Stripes */
  /*Fallback:
http://nimbupani.com/using-background-clip-for-text-with-css-fallback.html
*/
  background: -webkit-linear-gradient(transparent, transparent),               
  url(https://i.imgur.com/WyMyxQ6.png) repeat;
  /* clipping webkit magic */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  /* Animation */
  animation: glow 3s infinite alternate;
  animation-timing-function: cubic-bezier(1,.3,.79,.81);
}

@keyframes glow {
  0% {
    text-shadow: 0 0 1px rgba(255,255,255,.1),
                  0 0 3px rgba(255,255,255,.1),
                  0 0 5px rgba(157,208,154,.3);
  }
  50% {
    text-shadow: 0 0 2px rgba(255,255,255,.1),
                  0 0 5px rgba(255,255,255,.1);
  }
  52% {
    text-shadow: 0 0 5px rgba(157,208,154,.3);
  }
  54% {
    text-shadow: 0 0 2px rgba(255,255,255,.1),
                  0 0 5px rgba(255,255,255,.1);
  }
  100% {
    text-shadow: 0 0 2px rgba(255,255,255,.1),
                  0 0 5px rgba(255,255,255,.1),
                  0 0 10px rgba(157,208,154,.3);
  }
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}
.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-items: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 900px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
}

.coin-volume {
  width: 200px
}

.coin-marketcap {
  width: 240px;
}

.coin-percent {
  width: 80%;
}

.red {
  color: #f00606;
}

.green {
  color: green;
}
